import React, { Component } from 'react';
import CardList from './CardList';
import SearchBox from './SearchBox';
import './App.css'
import Scroll from './Scroll'

class App extends Component {
    constructor() {
        super()
        this.state = {
            robots: [],
            searchfield: ''
        }
    }
    componentDidMount() {
        fetch('https://jsonplaceholder.typicode.com/users').then(response => {
            //go to the url and get the jason file
            //transfer the jason file to jacascript object
            return response.json();
        })
            .then(user => {
                this.setState({ robots: user })
                // we can name "user" as anything we want, the react the the element of arry as a robot, 
                // s
            })

    }
    // use arrow function to create a scope so that "this" refers to App object. 
    //Otherwise it points to event,which does not have this.state 
    onSearchChange = (event) => {
        this.setState({ searchfield: event.target.value })

    }
    render() {
        const filteredRobots = this.state.robots.filter(robots => {
            return robots.name.toLocaleLowerCase().includes(this.state.searchfield.toLocaleLowerCase())
        })
        if (this.state.robots.length === 0) {
            return <h1 className='f1 tc'>Loading</h1>
        } else {
            return (
                <div className='tc'>
                    <h1 className='f1' >RoboFriends</h1>
                    <SearchBox searchChange={this.onSearchChange} />
                    <Scroll>
                        <CardList robots={filteredRobots} />
                    </Scroll>
                </div>
            );
        };
    }
};




export default App;